@import 'colors';
@import url('https://fonts.googleapis.com/css2?family=Philosopher:ital,wght@0,400;0,700;1,400;1,700&display=swap');

.timerContent{
    padding: $PaddingPhone;
    width: 100%;
    font-family: 'Philosopher';
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-size: 5vh;
}

.timerContent input[type=number]::-webkit-inner-spin-button,
.timerContent input[type=number]::-webkit-outer-spin-button
{
  -webkit-filter: invert(1);
  margin: 0;
}

.timerText{
    font-weight: 500;
    font-size: 40vh;
    text-align: center;
    margin: 2vh;

    width: 100vw;
    height: 80svh;
    display: flex;
    flex-direction: column;
    justify-content: center;

    align-items: center;
}

.timerBoxButton, .timerMakeValueContent, .timerValueContent {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    flex-wrap: wrap;

    margin: 25px;
}

.timerPlayButton, .timerReanitilizeButton, .timerLabelVolume, .timerAddButton, .timerDeleteValueButton, .timerMakeValueContent *{
    all: unset;

    text-align: center;
    vertical-align: middle;

    padding: 0 2vh;

    height: 100%;
}

.timerMakeValueContent input{
    width: auto; // Let the input width adjust to content
    max-width: 3em;
}

.timerLabelVolume{
    transform: rotate(180deg);
}

/* Styles de base du range slider */
input[type="range"] {
    -webkit-appearance: none;
    appearance: none;
    background-color: transparent;
    overflow: hidden;
    height: 16px;
    transition: all ease 100ms;
  }
  
  /* Barre du range slider */
  input[type="range"]::-webkit-slider-runnable-track {
    width: 100%;
    height: 4px;
    border-radius: 5px;
    background: $secondary-color;
    box-shadow: 0px 0px 8px rgba(255, 14, 14, 0.5); /* Ombre lumineuse */
    transition: all ease 100ms;
  }
  
  /* Curseur du range slider */
  input[type="range"]::-webkit-slider-thumb {
    
    -webkit-appearance: none;
    transition: all ease 100ms;
    height: 16px;
    width: 16px;
    border-radius: 22px;
    background: $primary-color;
    cursor: grab;
    margin-top: -6px;

    $widthThumbSlider: 60; // Remplacez cette valeur par le nombre de valeurs souhaité

    $box-shadow-values: ();
    
    @for $i from 0 to $widthThumbSlider {
      $x-offset: -$i * 2px;
      $y-offset: 0;
      $blur: 0;
      $widthThumb: -5px;
      
      $box-shadow-values: append(
        $box-shadow-values,
        #{$x-offset} #{$y-offset} #{$blur} #{$widthThumb} #{$primary-color},
        comma
      );
    }
    
    box-shadow: $box-shadow-values;


   }
  
  /* Effet au survol */
  input[type="range"]:hover::-webkit-slider-runnable-track {
    filter: brightness(180%); /* Légère augmentation de la luminosité */
  }
  
  input[type="range"]:hover::-webkit-slider-thumb {
    filter: brightness(120%); /* Légère augmentation de la luminosité sur le curseur */
  }
  